.login {
  width: 100%;
  height: 937px;
  background-image: url(/static/media/login.5245a1b7.jpg);
  background-size: 100% 100%;
}
.login .login-header {
  width: 100%;
  height: 80px;
  display: flex;
  align-items: center;
  background-color: rgba(17, 21, 13, 0);
}
.login .login-header img {
  width: 50px;
  height: 50px;
  margin: 0 10px 0 40px;
}
.login .login-header h1 {
  font-size: 25px;
  color: #FFFFFF;
  margin-bottom: 10px;
}
.login .login-content {
  width: 500px;
  height: 412px;
  opacity: 0.7;
  background: #ffffff;
  margin: 131px auto;
  padding: 20px 40px;
  text-align: center;
}
.login .login-content h3 {
  font-size: 40px;
  font-weight: 500;
  padding-bottom: 16px;
  text-align: center;
  margin-bottom: 30px;
  margin-top: 30px;
  color: #1890ff;
}
.login .login-content .ant-input-affix-wrapper {
  padding: 6px 11px;
  margin-bottom: 10px;
}
.login .login-content .login-form .login-form-button {
  width: 100%;
  border-radius: 4px;
}
.login .login-content .login-form .ant-btn {
  height: 40px;
}
